import React from 'react';
import { BrowserRouter as Router, Route } from "react-router-dom";
import './App.scss';
import NewUserForm from "./Forms/NewUserForm";
import UserLoginForm from "./Forms/UserLoginForm";
import NavMenu from "./Pages/NavMenu";
import StockTable from "./Tables/StockTable";
import NewStockForm from "./Forms/NewStockForm";
import NewCustomerForm from "./Forms/NewCustomerForm";
import ReportDisplay from "./Utils/ReportDisplay";
import ServiceProvider from "./service-provider";
import GenericCRUDTable from "./Tables/GenericCRUDTable";
import NewSupplierForm from "./Forms/NewSupplierForm";
import Home from "./Pages/Home";

function App() {
    const service_provider =  new ServiceProvider();
  return (
      <div>

          <Router>
              {/*<Route path="/" exact >*/}
              {/*    <UserLoginForm serviceProvider={service_provider}/>*/}
              {/*</Route>*/}
              <Route path="/" exact  >
                  <UserLoginForm serviceProvider={service_provider}/>
              </Route>
              <Route path="/home">
                  <NavMenu serviceProvider={service_provider}/>
                  <Home/>
              </Route>
              <Route path="/new-user" exact >
                  <NavMenu serviceProvider={service_provider}/>
                  <NewUserForm serviceProvider={service_provider} />
              </Route>
              <Route path="/stock-table" exact >
                  <NavMenu serviceProvider={service_provider}/>
                  <StockTable serviceProvider={service_provider} />
              </Route>
              <Route path="/new-stock" exact >
                  <NavMenu serviceProvider={service_provider}/>
                  <NewStockForm serviceProvider={service_provider}/>
              </Route>
              <Route path="/new-supplier" exact >
                  <NavMenu serviceProvider={service_provider}/>
                  <NewSupplierForm serviceProvider={service_provider}/>
              </Route>
              <Route path="/new-customer" exact  >
                  <NavMenu serviceProvider={service_provider}/>
                  <NewCustomerForm serviceProvider={service_provider} />
              </Route>

              <Route path="/customer-table" exact  >
                  <NavMenu serviceProvider={service_provider}/>

                  <GenericCRUDTable
                      resource="customer/"
                      serviceProvider={service_provider}
                      columns={["First Name" ,"Last Name","Loyalty Card Number"]}
                      title={"Customers"}
                  />
              </Route>
              <Route path="/supplier-table" exact  >
                  <NavMenu serviceProvider={service_provider}/>

                  <GenericCRUDTable
                      resource="supplier/"
                      serviceProvider={service_provider}
                      columns={["Name" ,"Phone","Address"]}
                      title={"Suppliers"}
                  />
              </Route>
              <Route path="/user-table" exact  >
                  <NavMenu serviceProvider={service_provider}/>

                  <GenericCRUDTable
                      resource="user/"
                      serviceProvider={service_provider}
                      columns={["First Name" ,"Last Name","Email"]}
                      title={"Users"}
                  />
              </Route>

              {/*Not yet needed*/}
              <Route path="/view-report" exact >
                  <ReportDisplay serviceProvider={service_provider}/>
              </Route>
          </Router>
      </div>

  );
}

export default App;
