import React from 'react';
import Table from "react-bootstrap/Table";
import FormControl from "react-bootstrap/FormControl";
import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import ServiceProvider  from '../service-provider.js'
import Modal from "react-bootstrap/Modal";

class StockTable extends React.Component{
    headings = ["StockName", "Selling Price", "Wholesale", "CostPrice", "QTY[POS]", "QTY[STORE]"];
    resource = "stock-item/";
    selectedStock = {name: ""};
    adjustMenuRadio = null;
    constructor(props) {
        super(props);
        this.api = new ServiceProvider();
        this.state = {
            showBarCodeModal: false,
            showAdjustQuantityModal: false,
            showDeleteStockModal: false,
            data:[],
            buttonsDisabled: true,
            adjustQuantityMenuDisabled: true,

        };
        this.stockTableRadioHandler = this.stockTableRadioHandler.bind(this);
        this.adjustMenuRadioHandler = this.adjustMenuRadioHandler.bind(this);
        this.adjustStock = this.adjustStock.bind(this);
        this.deleteStock = this.deleteStock.bind(this);
        this.handleSearch = this.handleSearch.bind(this);
    }
    async loadTableData(){
        this.setState({data : await this.props.serviceProvider.listAll(this.resource)});
        sessionStorage.setItem("stockData",JSON.stringify(this.state.data));
        this.selectedStock = {name: ""};
        this.setState({buttonsDisabled: true});
    }
    componentDidMount(){
       this.loadTableData();
    }
    async deleteStock(){
        await this.props.serviceProvider.deleteResource(this.resource, this.selectedStock.id);
        this.loadTableData();
        this.hideModal("Delete Stock");
    }
    async adjustStock(){
        let data = this.selectedStock;
        let adjQuantity =  Number(document.getElementById("adjustQuantityUnit").value);
        switch (this.adjustMenuRadio){
            case "adjQuantitySalesPoint":
               data.posQuantity = data.posQuantity+ adjQuantity;
                break;
            case "adjQuantityStore":
                data.storeQuantity = data.storeQuantity+ adjQuantity;
                break;
            default:
                break;
        }
        await this.props.serviceProvider.updateResource(this.resource, data.id, JSON.stringify(data));
        // TODO: need to create a web hook to fix this.
        this.hideModal("Adjust Quantity");
        this.loadTableData();
        data = null;
    }
    mapStock(){
        console.log("Map Stock Not Yet Implemented ")
    }
    stockTableRadioHandler($event){
        this.setState({ buttonsDisabled:false});
        this.selectedStock = this.state.data[$event.target.id];
    }
    adjustMenuRadioHandler($event){
        this.adjustMenuRadio = $event.target.id;
        console.log(this.adjustMenuRadio);
        this.setState({ adjustQuantityMenuDisabled: false});
    }
    showModal( option){
        switch (option){
            case "BarcodeMenu":
                this.setState({showBarCodeModal: true});
                break;
            case "Adjust Quantity":
                this.setState({showAdjustQuantityModal: true});
                this.setState({ adjustQuantityMenuDisabled: true});
                break;
            case "Delete Stock":
                this.setState({showDeleteStockModal: true});
                break;
            default:
                break;
        }
    }
    hideModal( option){
        switch (option){
            case "BarcodeMenu":
                this.setState({showBarCodeModal: false});
                break;
            case "Adjust Quantity":
                this.setState({showAdjustQuantityModal: false});
                break;
            case "Delete Stock":
                this.setState({showDeleteStockModal: false});
                break;
            default:
                break;
        }
    }
    handleSearch($event){
        let text = $event.target.value;
        let localData = JSON.parse(sessionStorage.getItem("stockData"));
        this.setState({data: localData.filter((stock) => {
                return stock.name.indexOf(text) !== -1;
            })
        });
        text = null;
        localData = null;

    }
    tableHtml(){
        return(
            <Table bordered size="sm">
                <thead>
                <tr>
                    <th> </th>
                    {this.headings.map((heading, index) => <th> {heading}</th>)}
                </tr>
                </thead>
                <tbody>
                    {
                        this.state.data.map((stock, index) => {
                            return(
                                <tr>
                                    <td><input name="type_radio" type="radio" id={index} onClick={this.stockTableRadioHandler}/></td>
                                    <td> {stock.name}</td>
                                    <td> {stock.sellingPrice}</td>
                                    <td> {stock.wholeSalePrice}</td>
                                    <td> {stock.costPrice}</td>
                                    <td> {stock.posQuantity}</td>
                                    <td> {stock.storeQuantity}</td>
                                </tr>
                            )
                        })
                    }
                </tbody>
            </Table>
        )
    }
    navHtml(){
        return(
            <div className={'text-center'}>
                <div >
                    <h1>Stock Table</h1>
                </div>
                <Form>
                    <Row sm='12'>
                        <Col sm='6'>
                            <Form.Group as={Row} controlId="stockTableFind">
                                <Col sm={2}>
                                    <Form.Label><h2>Search </h2></Form.Label>
                                </Col>
                                <Col sm={10}>
                                    <FormControl onChange={this.handleSearch} type="text"/>
                                </Col>

                            </Form.Group>
                        </Col>
                        <Col sm='6'>
                            <Form.Group as={Row} controlId="stockTableSort">
                                <Col sm={2}>
                                    <Form.Label><h2>Sort </h2></Form.Label>
                                </Col>
                                <Col sm={10}>
                                    <Form.Control as="select" custom>
                                        <option> </option>
                                        <option>1</option>
                                        <option>2</option>
                                        <option>3</option>
                                        <option>4</option>
                                        <option>5</option>
                                    </Form.Control>
                                </Col>
                            </Form.Group>
                        </Col>
                    </Row>
                </Form>
            </div>

        )

    }
    buttonHtml(){
        return(
            <Row>
                <Col  sm={'3'}>
                    <Button variant="secondary" disabled={this.state.buttonsDisabled } onClick={() => this.showModal("Delete Stock")}> Delete Stock</Button>
                </Col>
                <Col  sm={'3'}>
                    <Button variant="secondary" disabled={this.state.buttonsDisabled }  onClick={this.mapStock}> Stock Mappings</Button>
                </Col>
                <Col  sm={'3'}>
                    <Button variant="secondary" disabled={this.state.buttonsDisabled }  onClick={() => this.showModal("BarcodeMenu")} >BarCode Menu</Button>
                </Col>
                <Col sm={'3'}>
                    <Button variant="secondary" disabled={this.state.buttonsDisabled }  onClick={() => this.showModal("Adjust Quantity")}> Adjust Quantity</Button>
                </Col>
            </Row>
        )
    }
    barCodeMenuModalHtml(){
        return (
            <Modal show={this.state.showBarCodeModal} onHide={() => this.hideModal("BarcodeMenu")}>
                <Modal.Header closeButton>
                    <Modal.Title>BarCode Menu</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Row>
                        <Col>
                            <Row>
                                <Col>
                                    <Button variant="secondary">Generate</Button>
                                </Col>
                                <Col>
                                    <Button variant="secondary">Capture</Button>
                                </Col>
                            </Row>
                            <Row>
                                <Col>
                                    <Form.Control plaintext readOnly defaultValue="email@example.com" />

                                </Col>
                            </Row>
                            <Row>
                                <Col sm="8">
                                    <br/>
                                    <Button variant="secondary" block>Print Labels</Button>
                                </Col>
                                <Col sm="4">
                                    <Form.Label>Unit(s)</Form.Label>
                                    <Form.Control type="number" defaultValue="0" />
                                </Col>


                            </Row>
                        </Col>

                        <Col>
                            {/*Might need to style background color white*/}
                            <h3>
                                Musaroq
                            </h3>
                            <p>
                                Product Name
                                <br/>
                                Product Price
                            </p>
                        </Col>
                    </Row>
                </Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={ () => this.hideModal("BarcodeMenu")}>
                        Cancel
                    </Button>
                    <Button variant="primary" >
                        Save Changes
                    </Button>
                </Modal.Footer>
            </Modal>
        )
    }
    adjustQuantityModalHtml(){
        return (
            <Modal show={this.state.showAdjustQuantityModal} onHide={ () => this.hideModal("Adjust Quantity")}>
                <Modal.Header closeButton>
                    <Modal.Title>Adjust Quantity Menu</Modal.Title>
                </Modal.Header>

                <Modal.Body>
                    <Col>
                        <Row>
                            <Form.Group as={Col} controlId="adjustQuantityReason">
                                <Form.Label>Reason For Adjustment</Form.Label>
                                <Form.Control type="text"  />
                            </Form.Group>
                        </Row>
                        <Row style={{ paddingTop: '10px', paddingBottom: '10px'}}>
                            <Col sm="6">
                                <Row>
                                    <Col sm={2}>
                                        <input name="type_radio" type="radio" id={"adjQuantitySalesPoint"} onClick={this.adjustMenuRadioHandler}/>
                                    </Col>
                                    <Col sm={10}>
                                        <Form.Label>Sales Point</Form.Label>
                                    </Col>
                                </Row>
                            </Col>
                            <Col sm="6">
                                <Row>
                                    <Col sm={2}>
                                        <input name="type_radio" type="radio" id={"adjQuantityStore"} onClick={this.adjustMenuRadioHandler}/>
                                    </Col>
                                    <Col sm={10}>
                                        <Form.Label>Store</Form.Label>
                                    </Col>
                                </Row>
                            </Col>
                        </Row>

                        <Form.Group as={Row} controlId="adjustQuantityUnit">
                            <Col sm="3">
                                <Form.Label>Unit(s)</Form.Label>
                            </Col>
                            <Col sm="9">
                                <Form.Control type="number" defaultValue="0" />
                            </Col>
                        </Form.Group>
                    </Col>
                </Modal.Body>

                <Modal.Footer>
                    <Button variant="secondary" onClick={ () => this.hideModal("Adjust Quantity")}>
                        Cancel
                    </Button>
                    <Button variant="primary" disabled={this.state.adjustQuantityMenuDisabled} onClick={this.adjustStock}>
                        Adjust
                    </Button>
                </Modal.Footer>
            </Modal>
        )
    }
    deleteStockModalHtml(){
        return (
            <Modal show={this.state.showDeleteStockModal} onHide={() => this.hideModal("Delete Stock")}>
                <Modal.Header closeButton>
                    <Modal.Title>Delete Stock</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <h4>Are you sure you want to delete the stock {this.selectedStock.name}</h4>
                </Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={ () => this.hideModal("Delete Stock")}>
                        Cancel
                    </Button>
                    <Button variant="danger" onClick={this.deleteStock} >
                        Delete
                    </Button>
                </Modal.Footer>
            </Modal>
        )
    }
    render() {
        return(
            <div>
                {this.navHtml()}
                <div style={{ overflow: "scroll"}}>
                    {/*height:"100px", width:"100px",*/}
                    {this.tableHtml()}
                </div>
                {this.buttonHtml()}
                {this.barCodeMenuModalHtml()}
                {this.adjustQuantityModalHtml()}
                {this.deleteStockModalHtml()}
            </div>
        )
    }
}
export default StockTable;
