import React from 'react';
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";
import Container from "react-bootstrap/Container";

class NewSupplierForm extends React.Component{
    constructor(props) {
        super(props);
        this.formCenter = {
            marginTop: "150px"
        };
        this.register = this.register.bind(this);
        this.resource = "supplier/";
    }


    register($event){
        $event.preventDefault();
        let data = {
            name: document.getElementById("supplierName").value,
            phone: document.getElementById("supplierPhone").value,
            address: document.getElementById("supplierAddress").value,
        };
        this.props.serviceProvider.createResource(this.resource, JSON.stringify(data));
        data = null;
    }
    formHtml(){
        return (
            <Container fluid style={this.formCenter}>
                <Form>
                    <Col>
                        <Row className="justify-content-md-center">
                            <h2>New Supplier</h2>
                        </Row>
                        <Row className="justify-content-md-center">
                            <Form.Group controlId="supplierName">
                                <Form.Label>Supplier Name</Form.Label>
                                <Form.Control type="text"  />
                            </Form.Group>
                        </Row>

                        <Row className="justify-content-md-center">
                            <Form.Group controlId="supplierPhone">
                                <Form.Label>Supplier Tel.</Form.Label>
                                <Form.Control type="text"  />
                            </Form.Group>
                        </Row>

                        <Row className="justify-content-md-center">
                            <Form.Group controlId="supplierAddress">
                                <Form.Label>Address</Form.Label>
                                <Form.Control type="text"  />
                            </Form.Group>
                        </Row>
                        <Row className="justify-content-md-center">
                            <Button variant="secondary" style={{marginRight:'15px'}}>
                                Cancel
                            </Button>
                            <Button variant="primary" type="submit" onClick={this.register}>
                                Add
                            </Button>
                        </Row>
                    </Col>
                </Form>
            </Container>
        )
    }
    render() {
        return(
            <div>
                {this.formHtml()}
            </div>
        )
    }
}
export default NewSupplierForm;
