import React from 'react';
import Table from "react-bootstrap/Table";
import Button from "react-bootstrap/Button";
import FormControl from "react-bootstrap/FormControl";

class GenericCRUDTable extends React.Component{
    constructor(props) {
        super(props);
        // this.loadTableData();

        this.state = {data:[]};
        this.handleSearch = this.handleSearch.bind(this);
    }
    resource = this.props.resource;
    camelCasedColNames = [];
    colCount = this.props.columns.length;

    componentDidMount(){
        this.loadTableData();
        for (let i = 0; i < this.colCount; i++) {
            let col = this.props.columns[i].replace(/\s/g,'');
            this.camelCasedColNames.push(col.charAt(0).toLowerCase()+col.slice(1));
        }
    }
    async loadTableData(){
        this.setState({data : await this.props.serviceProvider.listAll(this.resource)});
        console.log(this.state);
        sessionStorage.setItem(this.resource+"Data",JSON.stringify(this.state.data));
    }
    handleSearch($event){
        let id = $event.target.id;
        let text = $event.target.value;
        let localData = JSON.parse(sessionStorage.getItem(this.resource+"Data"));
        this.setState({data: localData.filter((dataPoint) => {
                return dataPoint[id].toString().indexOf(text) !== -1;
        })});
        text = null;
        localData = null;
        id = null;
    }
    render() {
        return(
            <div className={'text-center'} >
                <div >
                    <h1>{this.props.title}</h1>
                </div>
                <Table  responsive striped size="sm" >
                    <thead>
                        <tr>
                            {this.props.columns.map((col, index) => <th key={this.props.resource+"Col"+index}> {col}</th>)}
                            <th/>
                        </tr>
                    </thead>
                    <tbody>
                      <tr>
                          {this.camelCasedColNames.map((col) =>  <td  key={col}><FormControl id={col} onChange={this.handleSearch} type="text"/></td>)}
                          <td>
                              <Button variant={"primary"} block style={{fontSize:"15px"}} href={"new-"+this.props.resource}>
                                  <i className="fas fa-plus"/>
                              </Button>
                          </td>
                      </tr>

                      {this.state.data.map((dataPoint, rowIndex) => {
                            return(
                                <tr key={this.props.resource+"Row"+rowIndex}>
                                    {this.camelCasedColNames.map((col, colIndex) => <td key={this.props.resource+"Cell["+rowIndex+"]["+colIndex+"]"}>{dataPoint[col]}</td>)}
                                    <td style={{width:"15%"}}>
                                        <Button variant="link" href={"new-"+this.resource+dataPoint.id} size={"sm"}>Edit</Button>
                                        <Button variant="link"  size={"sm"}>Transactions</Button>
                                    </td>
                                </tr>
                        )})}
                    </tbody>
                </Table>
            </div>
        )
    }
}
export default GenericCRUDTable;
