import React from 'react';
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";
import Container from "react-bootstrap/Container";

class NewCustomerForm extends React.Component{
    constructor(props) {
        super(props);
        this.formCenter = {
            marginTop: "70px"
        };
        this.register = this.register.bind(this);
        this.resource = "customer/";
    }


    register($event){
        $event.preventDefault();
        let data = {
            firstName: document.getElementById("customerFirstName").value,
            lastName: document.getElementById("customerLastName").value,
            age: document.getElementById("customerAge").value,
            phone: document.getElementById("customerPhone").value,
            address: document.getElementById("customerAddress").value,
            loyaltyCardNumber: document.getElementById("loyaltyCardNumber").value,
        };
        this.props.serviceProvider.createResource(this.resource, JSON.stringify(data));
        data = null;
    }

    formHtml(){
        return (
            <Container fluid style={this.formCenter}>
                <Form>
                    <Col>
                        <Row className="justify-content-md-center">
                            <h2>New Customer </h2>
                        </Row>

                        <Row className="justify-content-md-center">
                            <Form.Group controlId="customerFirstName">
                                <Form.Label>First Name</Form.Label>
                                <Form.Control type="text"  />
                            </Form.Group>
                        </Row>

                        <Row className="justify-content-md-center">
                            <Form.Group controlId="customerLastName">
                                <Form.Label>Last Name</Form.Label>
                                <Form.Control type="text"  />
                            </Form.Group>
                        </Row>

                        <Row className="justify-content-md-center">
                            <Form.Group controlId="customerAge">
                                <Form.Label>Age</Form.Label>
                                <Form.Control type="number"  />
                            </Form.Group>
                        </Row>

                        <Row className="justify-content-md-center">
                            <Form.Group controlId="customerPhone">
                                <Form.Label>Tel. </Form.Label>
                                <Form.Control type="number"  />
                            </Form.Group>
                        </Row>

                        <Row className="justify-content-md-center">
                            <Form.Group controlId="customerAddress">
                                <Form.Label>Address</Form.Label>
                                <Form.Control type="text"  />
                            </Form.Group>
                        </Row>

                        <Row className="justify-content-md-center">
                            <Form.Group controlId="loyaltyCardNumber">
                                <Form.Label>Loyalty Card Number</Form.Label>
                                <Form.Control type="text"  />
                            </Form.Group>
                        </Row>

                        <Row className="justify-content-md-center">
                            <Button variant="secondary" type="submit"  style={{marginRight:'20px'}}>
                                Cancel
                            </Button>
                            <Button variant="primary" type="submit" onClick={this.register} >
                                Add
                            </Button>
                        </Row>
                    </Col>
                </Form>
            </Container>
        )
    }
    render() {
        return(
            <div>
                {this.formHtml()}
            </div>
        )
    }
}
export default NewCustomerForm;
