import React from 'react';
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";
import Container from "react-bootstrap/Container";

class NewStockForm extends React.Component{
    constructor(props) {
        super(props);
        this.formCenter = {
            marginTop: "150px"
        };
        this.resource = "stock-item";
        this.createStockItem = this.createStockItem.bind(this);
    }

    async createStockItem($event){
        $event.preventDefault();
        let data =
            {
            name: document.getElementById("stockName").value,
            quantity_per_unit: document.getElementById("stockQuantityPerUnit").value,
            reorder_level: document.getElementById("stockReorderLevel").value,
        };
        let response = await this.props.serviceProvider.createResource(this.resource, JSON.stringify(data));

        //TODO: create a toast here based on the request response consider whether or not to reroute the user to the home page on success
        console.log(response);
        if(response) document.getElementById("newStockForm").reset();
        data = null;
        response = null;
    }
    formHtml(){
        return (
            <Container fluid style={this.formCenter}>
                <form id={"newStockForm"}>
                    <Col>
                        <Row className="justify-content-md-center">
                            <h2>New Stock Entry</h2>
                        </Row>

                        <Row className="justify-content-md-center">
                            <Form.Group controlId="stockName">
                                <Form.Label>Stock Name</Form.Label>
                                <Form.Control type="text"  />
                            </Form.Group>
                        </Row>

                        <Row className="justify-content-md-center">
                            <Form.Group controlId="stockQuantityPerUnit">
                                <Form.Label>Quantity Per Unit</Form.Label>
                                <Form.Control type="text"  />
                            </Form.Group>
                        </Row>

                        <Row className="justify-content-md-center">
                            <Form.Group controlId="stockReorderLevel">
                                <Form.Label>Re-Order Level</Form.Label>
                                <Form.Control type="number" />
                            </Form.Group>
                        </Row>
                        <Row className="justify-content-md-center" noGutters={true}>
                            <Col md={{span:1, offset:1}}>
                                <Button variant="secondary"  href={"/home"}>
                                    Cancel
                                </Button>
                            </Col>
                            <Col md={1}>
                                <Button variant="primary" type="submit" onClick={this.createStockItem}>
                                    Add
                                </Button>
                            </Col>

                        </Row>
                    </Col>
                </form>
            </Container>
        )
    }
    render() {
        return(
            <div>
                {this.formHtml()}
            </div>
        )
    }
}
export default NewStockForm;
