import React from 'react';
import { Document, Page } from 'react-pdf';
import samplePDF from './documentLarge.pdf';
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Form from 'react-bootstrap/Form';
// import * as path from 'path';
// import * as url from 'url';

class ReportDisplay extends React.Component{
    constructor(props){
        super(props);
        this.state = {numPages: 0, pageNumber: 1, zoomValue: 1};
        this.onDocumentLoadSuccess = this.onDocumentLoadSuccess.bind(this);
        this.nextPage = this.nextPage.bind(this);
        this.previousPage = this.previousPage.bind(this);
        this.handlePageNumUpdate = this.handlePageNumUpdate.bind(this);
        this.updateDisplayScale = this.updateDisplayScale.bind(this);
        this.printPage = this.printPage.bind(this);
    }
    zoomOptions = ["75%", "100%", "125%", "150%", "200%"];
    onDocumentLoadSuccess(doc ) {
        this.setState({numPages: doc._pdfInfo.numPages, pageNumber: 1});
    }

    changePage(offset) {
        this.setState({pageNumber: this.state.pageNumber + offset});
    }

    previousPage() {
        this.changePage(-1);
    }

    nextPage() {
        this.changePage(1);
    }

    updateDisplayScale(){

        try {
            let x = document.getElementById("zoomSelect").value;
            x = x.substring(0, x.length - 1);
            this.setState({zoomValue:parseInt(x)/100});
        }
        catch (e) {
            console.log(e);
        }

    }
    handlePageNumUpdate(){
        try {
            let x = parseInt(document.getElementById("pageNumForm").value);
            this.setState({pageNumber: (x)? x:0});
        }
        catch (e) {
            console.log(e);
        }

    }

    setPrint () {
        this.contentWindow.__container__ = this;
        this.contentWindow.onbeforeunload = this.closePrint;
        this.contentWindow.onafterprint = this.closePrint;
        this.contentWindow.focus(); // Required for IE
        this.contentWindow.print();
    }

    printPage () {
        var oHiddFrame = document.createElement("iframe");
        oHiddFrame.onload = this.setPrint;
        oHiddFrame.style.position = "fixed";
        oHiddFrame.style.right = "0";
        oHiddFrame.style.bottom = "0";
        oHiddFrame.style.width = "0";
        oHiddFrame.style.height = "0";
        oHiddFrame.style.border = "0";
        oHiddFrame.src = samplePDF;
        document.body.appendChild(oHiddFrame);
    }

    goBack(){

    }

    buttonColStyle = {
       backgroundColor: '#efefef',

    };
    toolbarHtml(){
        return(
            <Row  style={this.buttonColStyle}>

                <Col sm={3}>
                    <button type="button"   onClick={() => this.props.history.go(-1)}>
                        <i className="fas fa-arrow-circle-left" style={{fontSize: '20px'}}/>
                    </button>
                </Col>

                <Col sm={0.5}>
                    <button type="button"  disabled={this.state.pageNumber <= 1}  onClick={this.previousPage}>
                        <i className="fas fa-caret-left"/>
                    </button>
                </Col>



                <Col sm={3}>
                    <Row>
                        <Col sm={8} >
                            <Form.Control id="pageNumForm"  type="text" value={this.state.pageNumber} onChange={this.handlePageNumUpdate} size="sm"/>
                        </Col>
                        <Col>
                            / {(this.state.numPages )? this.state.numPages: "--" }
                        </Col>
                    </Row>
                </Col>

                <Col sm={0.5}>
                    <button type="button" disabled={this.state.pageNumber >= this.state.numPages} onClick={this.nextPage}>
                        <i className="fas fa-caret-right"/>
                    </button>
                </Col>

                <Col sm={1}>
                    <Form.Control id="zoomSelect" as="select" defaultValue={'100%'} size="sm" onChange={this.updateDisplayScale}>
                        {this.zoomOptions.map((option) => {return <option>{option}</option>})}
                    </Form.Control>
                </Col>

                <Col sm={{ span: 1}}>
                    <button type="button" onClick={this.printPage}>
                        <i className="fas fa-print"/>
                    </button>
                </Col>

                <Col>

                </Col>



            </Row>
        )
    }

    closePrint () {
        document.body.removeChild(this.__container__);
    }


    // TODO: File should be loaded from stream not from file system
    // TODO: Replace react-pdf with pdf-js
    render() {
        return(
            <div >

                {this.toolbarHtml()}
                <div style={{paddingLeft: '30%'}}>
                    <Document file={samplePDF} onLoadSuccess={this.onDocumentLoadSuccess}>
                        <Page pageNumber={this.state.pageNumber} scale={this.state.zoomValue}/>
                    </Document>
                </div>

            </div>
        )
    }
}
export default  ReportDisplay
