import React from 'react'
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import brandLogo from "../assets/logo.png";

class NewUserForm extends React.Component{
    constructor(props) {
        super(props);
        this.formCenter = {
            marginTop: "60px"
        };
        this.resource = "user/";
        this.register = this.register.bind(this);

    }

    register($event){
        $event.preventDefault();
        let data = {
            firstName: document.getElementById("userFirstName").value,
        lastName: document.getElementById("userLastName").value,
        email: document.getElementById("userEmail").value,
        hashedPassword :document.getElementById("userPassword").value,
        };
        this.props.serviceProvider.createResource(this.resource, JSON.stringify(data));
        data = null;
    }


    formHtml(){

        return (
            <div className={'container'} style={this.formCenter}>
                <div className="text-center">
                    <img src={brandLogo} className="rounded" alt="..."/>
                </div>
                <Form>
                    <Form.Group controlId="userFirstName">
                        <Form.Label>First Name</Form.Label>
                        <Form.Control type="text" placeholder="First Name" />
                    </Form.Group>

                    <Form.Group controlId="userLastName">
                        <Form.Label>Last Name</Form.Label>
                        <Form.Control type="text" placeholder="Last Name" />
                    </Form.Group>

                    <Form.Group controlId="userEmail">
                        <Form.Label>Email address</Form.Label>
                        <Form.Control type="email" placeholder="Enter email" />
                    </Form.Group>

                    <Form.Group controlId="userPassword">
                        <Form.Label>Password</Form.Label>
                        <Form.Control type="password" placeholder="Password" />
                    </Form.Group>

                    <Button variant="primary" type="submit" onClick={this.register}>
                        Create
                    </Button>
                </Form>
            </div>
        )
    }

    render(){
        return(
            <div>
                {this.formHtml()}
            </div>
        )
    }
}
export default NewUserForm
