// This file contains all the calls to the backed


export default class ServiceProvider {


    host = "http://127.0.0.1:8080/api/";
    //TODO: set initial authenticated state to false
    // authenticated = true;
    // credentials = {};
    // headers;
    constructor(){
        // sessionStorage.setItem("authenticated", "false")
        console.log(window.location.port);
    }

    async createResource(resource, data){
        let response = "";
        let headers = new Headers();
        headers.append('Authorization', JSON.parse(sessionStorage.getItem("header")));
        headers.append('Content-Type', 'application/json');
        try {
            if(JSON.parse(sessionStorage.getItem("authenticated"))){
                await fetch(this.host+resource, {
                    method: 'POST',
                    headers:headers,
                    body: data
                })
                    .then(reqResponse => {
                        return reqResponse.json();
                    })
                    .then((data) => response = data)
                    .catch(error => console.log("error " + error))
            }
        }
        catch (e) {
            console.log(e);
        }
        return response
    }

    async deleteResource(resource, id) {
        let response = "";
        let headers = new Headers();
        headers.append('Authorization', JSON.parse(sessionStorage.getItem("header")));
        try {
            if(JSON.parse(sessionStorage.getItem("authenticated"))){
                await fetch(this.host+resource+id, {
                        method: 'DELETE',
                        headers:headers,
                    }
                )
                    .then(reqResponse => {
                        return reqResponse.json();
                    })
                    .then((data) => response = data)
                    .catch(error => console.log("error " + error))
            }
        }
        catch (e) {
            console.log(e);
        }
        return response;
    }

    async updateResource(resource, id, data) {
        let response = "";
        let headers = new Headers();
        headers.append('Content-Type', 'application/json');
        headers.append('Authorization', JSON.parse(sessionStorage.getItem("header")));
        try {
            if(JSON.parse(sessionStorage.getItem("authenticated"))){
                await fetch(this.host+resource+id, {
                        method: 'PUT',
                        headers:headers,
                        body: data
                    }
                )
                    .then(reqResponse => {
                        return reqResponse.json();
                    })
                    .then((data) => response = data)
                    .catch(error => console.log("error " + error))
            }
        }
        catch (e) {
            console.log(e);
        }
        return response;
    }

    async listAll(resource){
        let response = [];
        let headers = new Headers();
        headers.append('Authorization', JSON.parse(sessionStorage.getItem("header")));
        try {
            if(JSON.parse(sessionStorage.getItem("authenticated"))){

                await fetch(this.host+resource, {
                    method: 'GET',
                    headers:headers,
                })
                    .then(reqResponse => {
                        // console.log("response "+response.json());
                        return reqResponse.json();
                    })
                    .then((data) => response = data)
                    .catch(error => console.log("error " + error))
            }
        }
        catch (e) {
            console.log(e);
        }
        return response
    }

    async authenticate(credentials){
        let name = "";
        //TODO: fix the unreliable errors surrounding the invalid credentials: Right now invalid credentials cause an error and return a blank username
        if(credentials){
            this.credentials = credentials;
            try{
                await fetch(this.host+"user/"+credentials.username, {
                    method: 'GET',
                    credentials: 'same-origin',
                    headers:{'Authorization': 'Basic '+ btoa(credentials.username+ ":" + credentials.password)}})
                    .then(response => {
                        return response.json();
                    })
                    .then((data) => {
                        if(data){
                            name = data;
                            sessionStorage.setItem("authenticated", "true");
                            sessionStorage.setItem("header", JSON.stringify('Basic '+ btoa(credentials.username+ ":" + credentials.password)));
                            // this.authenticated = true;
                            // this.headers = ;
                        }
                    })
                    .catch(error => console.log("error " + error));
            }
            catch (e) {
                console.log(e);
            }

        }else{
            // this.authenticated = false;
            // this.authorization = {};
        }
        return name;
    }

}
